import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router/index'
var moment = require("moment")
window.axios = require('axios')
axios.defaults.baseURL = 'https://snowmanserv.herokuapp.com/api';
axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLogged:false,
    token:null,
    programs:[],
    programs_stats:[],
    keys:[]
  },
  mutations: {
    changeToLogin (state,token) {
      state.isLogged = true
      state.token = token
      localStorage.setItem('token',token)
    },
    appendPrograms(state,programs){
      programs.forEach(el => {
        state.programs.push(el.name)
      }); 
    },
    appendKeys(state,keys){
      state.keys = keys
    },
    appendStats(state,data){
      var i = 0
        data.forEach(el => {
          var expires = 0
          var valid = 0
          el.keys.forEach(ell => {
             var ExpireData = moment(String(ell.Expire),'M/D/YYYY');
             var RealDate = moment(new Date().toLocaleDateString(),'M/D/YYYY');
             var diffDays = ExpireData.diff(RealDate, 'days');
             if(diffDays < 0) {
               expires = expires + 1
             } else {
               valid = valid + 1
             }
          })
         
          state.programs_stats.push({'id': ++i ,'prog':el.name,'keys':el.keys.length,'valids':valid,'expires':expires,'price':el.price})
        });
    }
  },
  actions: {
    login(context,pay) {
      axios.post('/users/login',{name:pay.name,pass:pay.pass}).then((res) => {
        if(res.status == 401){
          return
        }
        context.commit('changeToLogin',res.data.token)
        router.push('/')
      })
    },
    createProgram(context,pay) {
      axios.post('/programs/new',{name:pay.name,price:pay.price}).then((res) => {
        console.log(pay.name)
      })
    },
    generateKey(context,pay) {
      axios.post('/keys/create',{expireDate:pay.expire,program:pay.prog}).then((res) => {
        console.log(pay.name)
      })
    },
    getPrograms(context) {
      axios.get('/programs/all').then((res) => {
        context.commit('appendPrograms',res.data)
      })
    },
    getProgramsStat(context) {
      axios.get('/programs/all_stats').then((res) => {
        context.commit('appendStats',res.data)
        
      })
    },
    getKeysStat(context) {
      axios.get('/keys/stats').then((res) => {
        context.commit('appendKeys',res.data)
        
      })
    },
    newUpdate(context,pay) {
      axios.post('/updates/new',{prog:pay.program,version:pay.version,urge:pay.urge}).then((res) => {
      })
    },
    deleteProg(context,pay) {
      axios.delete('/programs/delete',{data:{name:pay.program}}).then((res) => {
      })
    },
    deleteKey(context,pay) {
      axios.delete('/keys/delete',{data:{KeyStr:pay.keys}}).then((res) => {
      })
    },
    editProg(context,pay) {
      axios.put('/programs/edit',{name:pay.prog,price:pay.price}).then((res) => {
        console.log(res.data)
      })
    },
  },
  modules: {
  }
})
